import { Module } from "vuex";
import { RootState } from "..";

export interface GuiVuex {
  openDrawer: boolean;
  showSearch: boolean;
  selectedCity: string;
}

export const guiModule: Module<GuiVuex, RootState> = {
  state: {
    openDrawer: false,
    showSearch: false,
    selectedCity: ""
  },
  mutations: {
    toggleDrawer(state, openDrawer = !state.openDrawer) {
      state.openDrawer = openDrawer;
    },
    toggleShowSearch(state, showSearch = !state.showSearch) {
      state.showSearch = showSearch;
    },
    updateSelectedCity(state, city) {
      state.selectedCity = city;
    }
  },
  actions: {
    //
  },
};

export const
  TOGGLE_DRAWER = "toggleDrawer", TOGGLE_SHOW_SEARCH = "toggleShowSearch", UPDATE_SELECTED_CITY= "updateSelectedCity";
