
import { AvatarService } from "@/services/AvatarService";
import AvatarCard from "@/components/AvatarCard.vue";
import { defineComponent } from "@vue/runtime-core";
import { TOGGLE_SHOW_SEARCH } from "@/store/modules/gui";
import FabButton from "@/components/FabButton.vue";
import CardsListTransition from "@/components/CardsListTransition.vue";

export default defineComponent({
  data() {
    return { expandedFab: true };
  },
  components: {
    AvatarCard,
    FabButton,
    CardsListTransition,
  },
  computed: {
    avatars() {
      const avatarService = new AvatarService();
      const city = this.$store.state.gui.selectedCity;

      if (!city || city === "") {
        return avatarService.avatarList;
      }
      return avatarService.getAvatarsByCity(city);
    },
  },
  methods: {
    search() {
      this.$store.commit(TOGGLE_SHOW_SEARCH, true);
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    onGesture(gesture: { x: number; y: number }) {
      if (gesture.y > 50) {
        this.expandedFab = true;
      }
      if (gesture.y < -50) {
        this.expandedFab = false;
      }
    },
    goToAvatarPage(avatarId: number) {
      this.$store.commit(TOGGLE_SHOW_SEARCH, false);
      this.$router.push(`/avatar/${avatarId}`);
    }
  },
});
