import { Avatar } from "@/model/Avatar";
import mockData from "./MOCK_DATA.json";

export class AvatarService {
    avatarList: Avatar[] = mockData as Avatar[];

    getCities() {
        return Array.from(new Set(this.avatarList.map(av => av.city)));
    }

    getAvatarsByCity(city: string) {
        return this.avatarList.filter(av => av.city === city);
    }
    getAvatarById(id: number) {
        return this.avatarList.filter(av => av.id === id)[0];
    }
}