
import { defineComponent } from "@vue/runtime-core";
import "@material/mwc-icon-button-toggle";
import {
  TOGGLE_DRAWER,
  TOGGLE_SHOW_SEARCH,
  UPDATE_SELECTED_CITY,
} from "@/store/modules/gui";
// import TextField from "./TextField.vue";
import OptionsInput from "./OptionsInput.vue";
import { AvatarService } from "@/services/AvatarService";

export default defineComponent({
  components: { /* TextField, */ OptionsInput },
  computed: {
    drawerOpened() {
      return this.$store.state.gui.openDrawer;
    },
    showSearch() {
      return this.$store.state.gui.showSearch;
    },
    cities(): string[] {
      const avSer = new AvatarService();
      return avSer.getCities();
    },
    badge() {
      return (
        this.$store.state.settings.installEvent != null ||
        this.$store.state.settings.updateAvailable
      );
    },
  },
  methods: {
    toggleDrawer() {
      this.$store.commit(TOGGLE_DRAWER);
    },
    toggleShowSearch() {
      this.$store.commit(TOGGLE_SHOW_SEARCH);
    },
    filterSelected(ev: CustomEvent) {
      this.$store.commit(
        UPDATE_SELECTED_CITY,
        this.cities[ev.detail.index - 1]
      );
    },
  },
});
