
import { TOGGLE_DRAWER, TOGGLE_SHOW_SEARCH } from "@/store/modules/gui";
import { defineComponent } from "@vue/runtime-core";

export default defineComponent({
  computed: {
    installEv() {
      return this.$store.state.settings.installEvent;
    },
    updateAvailable() {
      return this.$store.state.settings.updateAvailable;
    },
    version() {
      return this.$store.state.settings.version;
    },
  },
  methods: {
    installApp() {
      if (this.installEv) {
        this.installEv.prompt();
      }
    },
    updateApp() {
      if (this.updateAvailable) {
        window.location.reload();
      }
    },
    search() {
      if (this.$route.name !== "home") {
        this.$router.push("/");
      }
      this.$store.commit(TOGGLE_DRAWER, false);
      this.$store.commit(TOGGLE_SHOW_SEARCH, true);
    },
    toBookings() {
      this.$store.commit(TOGGLE_DRAWER, false);
      this.$router.push("/bookings");
    },
  },
});
