
import { defineComponent } from "@vue/runtime-core";
import { AvatarService } from "@/services/AvatarService";
import RatingStars from "@/components/RatingStars.vue";
import FabButton from "@/components/FabButton.vue";
import "@material/mwc-textfield";
import "@material/mwc-textarea";
import { ADD_BOOKING } from "@/store/modules/user";
import { Booking } from "@/model/Booking";

export default defineComponent({
  components: { RatingStars, FabButton },
  data() {
    const avatarService = new AvatarService();
    const avatar = avatarService.getAvatarById(
      parseInt(this.$route.params.id as string)
    );
    return { avatar, isBooking: false, bookDate: 0, bookDescription: "" };
  },
  methods: {
    book() {
      if (!this.isBooking) {
        this.isBooking = true;
        // this.$router.push({path: "/avatar/1/booking"});
      } else {
        this.$store.commit(
          ADD_BOOKING,
          new Booking({
            date: new Date(this.bookDate).getTime(),
            avatarId: this.avatar.id,
            description: this.bookDescription,
          })
        );
        this.$router.push("/bookCompleted");
      }
    },
  },
  /* computed: {
    isBooking() {
      return this.$route.path.includes("booking");
    },
  }, */
});
