import { Booking } from "@/model/Booking";
import { Module } from "vuex";
import { RootState } from "..";
import { loadObjFromLocal, withSaveObjLocal } from "../utils";

export interface UserVuex {
  bookings: Booking[];
}

export const userModule: Module<UserVuex, RootState> = {
  state: {
    bookings: loadObjFromLocal<Booking[]>('bookings', []),
  },
  mutations: {
    addBooking: withSaveObjLocal('bookings', (state, booking) => {
      state.bookings.push(booking);
    }),
    removeBooking: withSaveObjLocal('bookings', (state, bookingId) => {
      state.bookings.splice(state.bookings.map(b => b.id).indexOf(bookingId), 1);
    }),
  },
  actions: {
    //
  },
};

export const
  ADD_BOOKING = "addBooking", REMOVE_BOOKING = "removeBooking";
