
import { Booking } from "@/model/Booking";
import { AvatarService } from "@/services/AvatarService";
import { REMOVE_BOOKING } from "@/store/modules/user";
import { defineComponent, PropType } from "@vue/runtime-core";
export default defineComponent({
  props: {
    book: { required: true, type: Object as PropType<Booking> },
  },
  data() {
    return { expanded: false };
  },
  computed: {
    avatar() {
      const avService = new AvatarService();
      return avService.getAvatarById(this.book.avatarId);
    },
  },
  methods: {
    deleteBook() {
      this.$store.commit(REMOVE_BOOKING, this.book.id);
    },
  },
});
