
import { defineComponent } from "@vue/runtime-core";
import "@material/mwc-drawer";
import { TOGGLE_DRAWER } from "@/store/modules/gui";
import DrawerContent from "./DrawerContent.vue";

export default defineComponent({
  components: { DrawerContent },
  mounted() {
    (this.$refs.drawer as HTMLElement).addEventListener(
      "MDCDrawer:closed",
      this.onClose
    );
  },
  computed: {
    isOpened() {
      return this.$store.state.gui.openDrawer;
    },
  },
  methods: {
    onClose() {
      this.$store.commit(TOGGLE_DRAWER, false);
    },
    onGesture(gesture: { x: number; y: number }) {
      if (gesture.x < -50) {
        this.$store.commit(TOGGLE_DRAWER, false);
      }
    },
  },
});
