export class Booking {
    id: string;
    date: number;
    avatarId: number;
    description: string;

    constructor({ date, avatarId, description }: { date: number; avatarId: number; description: string; }) {
        this.id = Math.random().toString(36).replace('0.', '');
        this.date = date;
        this.avatarId = avatarId;
        this.description = description;
    }
}